import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { ButtonType } from '@shared/components/button/button.component';
import { debounceTime, Observable, of, switchMap, takeUntil } from 'rxjs';
import { PlaceAdFacade } from '@feature/place-ad/place-ad.facade';
import { DestroyService } from '@shared/services/destroy.service';
import { Ericode } from '@core/models';
import { PropertyWorthFacade } from '@feature/property-worth/property-worth.facade';
import { DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE } from '@angular/material/core';
import { NativeDateAdapter } from '@angular/material/core'; // Import MatNativeDateAdapter

// Custom date adapter based on the native DateAdapter
export class MyDateAdapter extends NativeDateAdapter {
	override format(date: Date, displayFormat: Object): string {
		const YEAR = date.getFullYear();

		return `${YEAR}`;
	}
}

export const MY_FORMATS = {
	parse: {
		dateInput: 'yyyy',
	},
	display: {
		dateInput: 'yyyy',
		monthYearLabel: 'yyyy',
		dateA11yLabel: 'LL',
		monthYearA11yLabel: 'yyyy',
	},
};

@Component({
    selector: 'findqo-property-information',
    templateUrl: './property-information.component.html',
    styleUrl: './property-information.component.scss',
    providers: [
        {
            provide: DateAdapter,
            useClass: MyDateAdapter,
            deps: [MAT_DATE_LOCALE],
        },
        {
            provide: MAT_DATE_FORMATS,
            useValue: MY_FORMATS,
        },
    ],
    standalone: false
})
export class PropertyInformationComponent implements OnInit {
	constructor(
		private fb: FormBuilder,
		private placeAdFacade: PlaceAdFacade,
		private destroy: DestroyService,
		private propertyWorth: PropertyWorthFacade
	) {}

	propertyInformationForm = this.fb.group({
		address: ['', [Validators.required]],
		countyArea: ['', [Validators.required]],
		county: ['', [Validators.required]],
		eirCode: [''],

		stand: ['', [Validators.required]],
		bedroomCount: [0, [Validators.required]],
		bathroomCount: [0, [Validators.required]],

		propertySize: [undefined, [Validators.min(1), Validators.pattern('^[0-9.]*$')]],
		propertyUnit: [''],

		purchasePrice: [undefined, [Validators.min(1), Validators.pattern('^[0-9.]*$')]],

		purchaseYear: [],

		improvements: [undefined],
		improvementCost: ['', [Validators.min(1), Validators.pattern('^[0-9.]*$')]],
		improvementDetail: [''],
	});

	@Output() nextTab = new EventEmitter();

	residentialsStands = {};
	propertyUnits = [];
	maxDate = new Date();

	readonly buttonType = ButtonType;

	addressErCode = new FormControl('', [Validators.required]);

	eirForceSearched = false;

	eirCodeOptions$: Observable<Ericode[]>;

	getEirCodes(text: string, params: string) {
		return this.placeAdFacade.getEricode(text, params);
	}

	callEricodeApi(): void {
		this.addressErCode?.valueChanges
			.pipe(
				debounceTime(1000),
				switchMap((searchText: string) => {
					if (!searchText) {
						return of([]);
					}

					if (searchText && searchText.trim() === '') {
						// If the address is empty, return an empty observable
						return of([]);
					} else {
						/**
						 * On change of Input, we are resetting the eirForce Searched
						 */
						this.eirForceSearched = true;
						return this.getEirCodes(searchText, '0');
					}
				}),
				takeUntil(this.destroy)
			)
			.subscribe((eirCodes: Ericode[]) => {
				this.eirCodeOptions$ = of(eirCodes);
			});
	}

	onErCodeSearch() {
		if (this.addressErCode.invalid || !this.eirForceSearched) {
			return;
		}

		this.eirForceSearched = false;

		const SEARCHED_KEYWORD = this.addressErCode.value;
		if (SEARCHED_KEYWORD) {
			this.getEirCodes(SEARCHED_KEYWORD, '1').subscribe();
		}
	}

	onUpdateRadioBtn(fromRadioButton: boolean): void {
		this.propertyInformationForm.get('improvements').setValue(fromRadioButton);

		if (fromRadioButton) {
			this.propertyInformationForm.get('improvementCost').setValidators([Validators.required, Validators.min(1), Validators.pattern('^[0-9]*$')]);
			this.propertyInformationForm.get('improvementCost').setErrors({ required: true });

			this.propertyInformationForm.get('improvementDetail').setValidators([Validators.required]);

			this.propertyInformationForm.updateValueAndValidity();
		} else {
			this.propertyInformationForm.get('improvementCost').setValue('');
			this.propertyInformationForm.get('improvementCost').clearValidators();
			this.propertyInformationForm.get('improvementCost').setErrors(null);

			this.propertyInformationForm.get('improvementDetail').setValue('');
			this.propertyInformationForm.get('improvementDetail').clearValidators();
			this.propertyInformationForm.get('improvementDetail').setErrors(null);

			this.propertyInformationForm.updateValueAndValidity();
		}
	}

	yearFilter = (date: Date | null): boolean => {
		return date ? date.getDate() === 1 && date.getMonth() === 0 : false;
	};

	onSetYear(normalizedYear, datepicker) {
		if (normalizedYear) {
			const ctrlValue = this.propertyInformationForm.getRawValue().purchaseYear || new Date(); // Default to current date if empty
			ctrlValue.setFullYear(normalizedYear.getFullYear());
			this.propertyInformationForm.patchValue({
				purchaseYear: ctrlValue,
			});

			datepicker.close(normalizedYear);
		}
	}

	onupdateAddress(event, eirCodeList) {
		const selectedAddressEricode = eirCodeList.find((eirCode) => eirCode.address === event.option.value);

		this.propertyInformationForm.patchValue({
			address: event.option.value,
			countyArea: selectedAddressEricode?.countyArea,
			county: selectedAddressEricode?.county,
			eirCode: selectedAddressEricode?.eirCode,
		});
	}

	onEditAddress() {
		this.propertyInformationForm.patchValue({
			address: '',
			countyArea: '',
			county: '',
			eirCode: '',
		});

		this.addressErCode.reset();
	}

	onNext() {
		const FORM_DATA = this.propertyInformationForm.value;

		this.nextTab.emit({
			status: this.propertyInformationForm.status,
			value: {
				...FORM_DATA,
				...(FORM_DATA.purchaseYear ? { purchaseYear: FORM_DATA.purchaseYear.getFullYear() } : {}),
				...(FORM_DATA.improvements ? { improvements: 1 } : { improvements: 0 }),
			},
			form: 'propertyInformation',
		});
	}

	onIncreaseCount(field) {
		this.propertyInformationForm.patchValue({
			[field]: this.propertyInformationForm.getRawValue()[field] + 1,
		});
	}

	onDecreaseCount(field) {
		this.propertyInformationForm.patchValue({
			[field]: this.propertyInformationForm.getRawValue()[field] - 1,
		});
	}

	ngOnInit() {
		this.callEricodeApi();

		this.propertyWorth.getSectionList().subscribe((res) => {
			const STANDS = res.filter((res) => res.reference === 'sell-residential');
			this.residentialsStands = STANDS[0];
		});

		this.propertyWorth.getFilterUnits().subscribe((res) => {
			this.propertyUnits = res;

			if (res && res?.length) {
				this.propertyInformationForm.patchValue({
					propertyUnit: res.find((res) => res.reference === 'square-meters')?.reference,
				});
				this.propertyInformationForm.addControl('propertySize', this.fb.control(undefined, [Validators.max(1000)]));
			}
		});

		this.propertyInformationForm.get('propertyUnit').valueChanges.subscribe((unit) => {
			if (unit === 'square-meters') {
				this.propertyInformationForm.addControl('propertySize', this.fb.control(undefined, [Validators.max(1000)]));
			}
		});

		this.propertyInformationForm.get('stand').valueChanges.subscribe((res) => {
			if (res === 'site') {
				this.propertyInformationForm.patchValue({
					bathroomCount: 0,
					bedroomCount: 0,
				});
			} else {
				this.propertyInformationForm.patchValue({
					bathroomCount: 1,
					bedroomCount: 1,
				});
			}
		});
	}
}
