import { Component } from '@angular/core';
import { PropertyWorthState } from '@core/data-access/states/property-worth.state';
import { ViewportService } from '@core/services/viewport.service';
import { ButtonType } from '@shared/components/button/button.component';
import { Observable } from 'rxjs';

@Component({
    selector: 'findqo-property-worth',
    templateUrl: './property-worth.component.html',
    styleUrl: './property-worth.component.scss',
    standalone: false
})
export class PropertyWorthComponent {
	constructor(
		public viewPort: ViewportService,
		public propertyWorthState: PropertyWorthState
	) {}

	readonly buttonType = ButtonType;

	selectedIndex = 0;
	formData;
	propertyInformation;
	aboutYou;

	isLoading$: Observable<boolean> = this.propertyWorthState.isLoading$;
	isError$: Observable<boolean> = this.propertyWorthState.isError$;

	onPrevTab(event: number) {
		this.selectedIndex = event;
	}

	onTabChange(tabEvent) {
		this.selectedIndex = tabEvent.index;
	}

	onNextTab(data, index: number) {
		if (data && data['status']) {
			if (data['form'] === 'propertyInformation') {
				this.propertyInformation = data;
			} else if (data['form'] === 'aboutYou') {
				this.aboutYou = data;

				this.formData = {
					...this.propertyInformation.value,
					...this.aboutYou.value,
				};
			}

			/**
			 * Added a setTimeout to enable the next tab first then move to next tab.
			 */
			setTimeout(() => {
				this.selectedIndex = index;
			}, 0);
		}
	}
}
